import FolderOutlined from '@ant-design/icons/lib/icons/FolderOutlined';
import { Button, Col, Row, Tag } from 'antd';
import { graphql, Link, useStaticQuery } from 'gatsby';
import Image, { FluidObject } from 'gatsby-image';
import parse from 'html-react-parser';
import React from 'react';
import Layout from '../components/Layout';
import SEO from '../components/SEO';
import Sidebar from '../components/Sidebar';
import TopSection from '../components/TopSection';
import { CategoryTagInfo, Post } from '../contracts/post';
import '../styles/blog.scss';
import { capitalizeFirstLetter, decodeHtmlCharCodes } from '../utils';

export interface Props {
	pathContext: {
		group: { node: Post }[];
		index: number;
		pageCount: number;
	};
	location: Location;
}

export const BlogPostsPage = (props: Props) => {
	const { group, index, pageCount } = props.pathContext;
	const previousUrl = index - 1 === 1 ? '' : (index - 1).toString();
	const nextUrl = (index + 1).toString();
	const { site } = useStaticQuery(graphql`
      query {
        site {
          siteMetadata {
            title
            description
          }
        }
      }
	`);

	return (
		<>
			<TopSection />
			<Layout location={props.location}>
				<SEO isHomePage={true} title={site.siteMetadata.title} description={site.siteMetadata.description} />
				<Row gutter={36} className="posts">
					<Col xs={24} sm={24} md={24} lg={16} xl={18} xxl={18} id="primary" className="content-area with-sidebar">
						{group.map(({ node }: { node: Post }) => {
							const fluid: FluidObject | null = (node.featuredImage && node.featuredImage.node && node.featuredImage.node.localFile && node.featuredImage.node.localFile.childImageSharp && node.featuredImage.node.localFile.childImageSharp.fluid) ? node.featuredImage.node.localFile.childImageSharp.fluid : null;
							const categories: CategoryTagInfo[] = (node.categories && node.categories.nodes) ? node.categories.nodes.filter((category) => category.name !== 'Uncategorized') : new Array<CategoryTagInfo>();
							const hasImage = (fluid && fluid.src && fluid.src.length > 0);
							return (
								<article className="post" key={node.slug}>
									<Row gutter={[30, 50]} justify="start" align="middle">
										{(fluid && fluid.src && fluid.src.length > 0) && (
											<Col xs={24} sm={24} md={9} lg={9}>
												<Link to={`/${node.slug}`} title={node.slug}>
													<Image className="shadow-image" fluid={fluid} alt={node.title} title={node.title} />
												</Link>
											</Col>
										)}
										<Col md={hasImage ? 15 : 24} lg={hasImage ? 15 : 24}>
											<div className="categories-container tags-container post-meta-container margin-bottom-24px">
												{categories && categories.length > 0 && categories.map((category, categoryIndex) => {
													return (
														<Tag key={categoryIndex}>
															<Link to={`/category/${category.slug}`} title={category.name}>
																<FolderOutlined />{capitalizeFirstLetter(category.name)}
															</Link>
														</Tag>
													);
												})}
											</div>
											<Link to={`/${node.slug}`} title={node.slug}>
												<h2 className="post-title black-color">{decodeHtmlCharCodes(node.title)}</h2>
											</Link>
											<div className="post-excerpt">{parse(node.excerpt)}</div>
											<span className="post-meta margin-left-2px">
												<span>By </span>
												<span className="author">
													<Link to={`/author/${node.author.node?.slug}`} title={node.author.node?.name}>
														{node.author.node?.name}
													</Link>
												</span>
												<span className="separator"></span>
												<span className="date">{(node.modified && node.modified.length > 0) ? node.modified : node.date}</span>
											</span>
										</Col>
									</Row>
								</article>
							);
						})}
						<div className="navigation-links">
							{index > 1 && (
								<div className="previous-link">
									<Link to={`/${previousUrl}`} title={`/${previousUrl}`}>
										<Button type="primary">Go to Previous Page</Button>
									</Link>
								</div>
							)}
							{index <= (pageCount - 1) && (
								<div className="next-link">
									<Link to={`/${nextUrl}`} title={`/${nextUrl}`}>
										<Button type="primary">Go to Next Page</Button>
									</Link>
								</div>
							)}
						</div>
					</Col>
					<Col xs={0} sm={0} md={0} lg={8} xl={6} xxl={6} id="secondary" className="sidebar">
						<Sidebar />
					</Col>
				</Row>
			</Layout>
		</>
	);
};

export default BlogPostsPage;
